import { Interceptors } from '@/service/Interceptors';
import { message } from 'ant-design-vue'; // 

export class HttpService {
  public axios: any;

  constructor() {
    // axios
    this.axios = new Interceptors().getInterceptors();
  }

  /**
   * get.
   * java
   * @param params  
   * @param jwt   token
   * @param url     url
   */
  public get(url: string, params: object, jwt = false) {
    return new Promise((resolve, reject) => {
      this.axios
        .get(url, {
          data: params,
          headers: { isJwt: jwt }
        })
        .then((res: any) => {
          this.resultHandle(res, resolve);
        })
        .catch((err: any) => {
          reject(err.message);
        });
    });
  }
  /**
   * get
   * url
   * @param params  
   * @param jwt   token
   * @param url     url
   */
  public getStr(url: string, params: object, jwt = false) {
    return new Promise((resolve, reject) => {
      this.axios
        .get(url, {
          params: params,
          headers: { isJwt: jwt }
        })
        .then((res: any) => {
          this.resultHandle(res, resolve);
        })
        .catch((err: any) => {
          reject(err.message);
        });
    });
  }
  /**
   * post
   * @param params  
   * @param jwt   token
   * @param url     url
   */
  public post(url: string, params: object, jwt = false) {
    return new Promise((resolve, reject) => {
      this.axios
        .post(url, params, {
          headers: { isJwt: jwt }
        })
        .then((res: any) => {
          this.resultHandle(res, resolve);
        })
        .catch((err: any) => {
          reject(err.message);
        });
    });
  }

  /**
   * java
   * delete
   * @param params  
   * @param jwt   token
   * @param url     url
   */
  public delete(url: string, params: object, jwt = false) {
    return new Promise((resolve, reject) => {
      this.axios
        .delete(url, {
          data: params,
          headers: { isJwt: jwt }
        })
        .then((res: any) => {
          this.resultHandle(res, resolve);
        })
        .catch((err: any) => {
          reject(err.message);
        });
    });
  }

  /**
   * url
   * delete
   * @param params  
   * @param jwt   token
   * @param url     url
   */
  public deleteStr(url: string, params: object, jwt = false) {
    return new Promise((resolve, reject) => {
      this.axios
        .delete(url, {
          params: params,
          headers: { isJwt: jwt }
        })
        .then((res: any) => {
          this.resultHandle(res, resolve);
        })
        .catch((err: any) => {
          reject(err.message);
        });
    });
  }

  /**
   * put
   * @param params  
   * @param jwt   token
   * @param url     url
   */
  public put(url: string, params: object, jwt = false) {
    return new Promise((resolve, reject) => {
      this.axios
        .put(url, params, {
          headers: { isJwt: jwt }
        })
        .then((res: any) => {
          this.resultHandle(res, resolve);
        })
        .catch((err: any) => {
          reject(err.message);
        });
    });
  }

  /**
   * patch
   * @param params  
   * @param jwt   token
   * @param url     url
   */
  public patch(url: string, params: object, jwt = false) {
    return new Promise((resolve, reject) => {
      this.axios
        .patch(url, params, {
          headers: { isJwt: jwt }
        })
        .then((res: any) => {
          this.resultHandle(res, resolve);
        })
        .catch((err: any) => {
          reject(err.message);
        });
    });
  }

  /**
   *
   * @param res
   * @param resolve
   */
  public resultHandle(res: any, resolve: any) {
    if (res.code === 200) {
      resolve(res.data);
    } else {
      this.errorHandle(res);
    }
  }

  /**
   * ,,(http,)
   * @param res
   */
  public errorHandle(res: any) {
    message.warn(res.msg); // ,
    // 
    // switch (res.status) {
    //   case -102:
    //     console.log(-102);
    //     break;
    //   case -152:
    //     console.log(-152);
    //     break;
    //   case -153:
    //     console.log(-153);
    //     break;
    //   case -154:
    //     console.log(-154);
    //     break;
    //   default:
    //     console.log(200);
    // }
  }
}
